import Vue from "vue";
import Vuex from "vuex";
//then you use Vuex
Vue.use(Vuex);
 
export default new Vuex.Store({
    state: {
        hints: [
            {
                category: "Mime",
                name: "Instrumenter",
                firstHint: "Basun",
                secondHint: "Cello",
                thirdHint: "Trompet",
                fourthHint: "Banjo"
            }
        ]
    },
    mutations: {
        addHint (state,newHint) {
            state.hints.push(newHint)
        }
    }
});