/<template>
    <div class="hello">
  <h1>Create Hint</h1>
       <b-form-input class="mx-auto" v-model="inputCategory" placeholder="Type of Hint (Nynne, Mime, Tale, etc)" style="text-align:center; width: 70%; display:flex; margin:20px"  ></b-form-input>
       <b-form-input class="mx-auto" v-model="inputName" placeholder="Category of Hint" style="text-align:center; width: 70%; display:flex; margin:20px"> asdf</b-form-input>
    <b-form-input class="mx-auto" v-model="inputFirstHint" placeholder="First Hint" style="text-align:center; width: 70%; display:flex; margin:20px">asd </b-form-input>
    <b-form-input class="mx-auto" v-model="inputSecondHint" placeholder="Second Hint" style="text-align:center; width: 70%; display:flex; margin:20px">asdf </b-form-input>
    <b-form-input class="mx-auto" v-model="inputThirdHint" placeholder="Third Hint" style="text-align:center; width: 70%; display:flex; margin:20px"> asdf </b-form-input>
    <b-form-input class="mx-auto" v-model="inputFourthHint" placeholder="Fourth Hint" style="text-align:center; width: 70%; display:flex; margin:20px"> asdf </b-form-input>

    <b-button class="m-2" v-on:click="CreateHint()">Gem nyt hint</b-button>
    </div>
</template>

<script>
import { db } from '../db'

export default {
    name: "CreateHint",
    data: function () {
        return{
        inputCategory: "",
        inputName: "",
        inputFirstHint: "",
        inputSecondHint: "",
        inputThirdHint: "",
        inputFourthHint: "",
        documents: [],
        }
    },
    firestore: {
        documents: db.collection('documents'),
    },
    methods: {
        CreateHint: function () {
      let hintObject = {};

      hintObject.category = this.inputCategory;
      hintObject.name = this.inputName;
      hintObject.firstHint = this.inputFirstHint;
      hintObject.secondHint = this.inputSecondHint;
      hintObject.thirdHint = this.inputThirdHint;
      hintObject.fourthHint = this.inputFourthHint;
      hintObject.status = false;
      this.$store.commit('addHint',hintObject);
      console.log(this.documents)




    }
    }
}
</script>

